import Registry from "../core/registry/registry";
import Module from "../core/registry/module";
import {serializeSymbol, deserializeSymbol} from "../core/communication/extendedJSON";

export default class TestModule extends Module{
    constructor(request, name){
        super(request, true);
        this.name = name;
    }
    setSomething(something){
        this.something = something;
        console.log(`something is now ${something}`);
        return 6;
    }
    alert(text){
        window.alert(text);
    }
    getSomething(){
        return this.something;
    }
}
export const config = {
    type: "alert",
    filter: request=>{
        return true;
    }
};
